* {
  font-family: 'Quicksand', Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: #eaeaea;
}

ul li{
  list-style-type: none;
}

#header{
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  width: 300px;
  background-color: #fff;
}

#this-month{
  margin: 20px;
  font-size: 1.3em;
  font-weight: 600;
}

#totalDep, #totalRev, #balance{
  margin: 10px 0px;
  padding: 15px;
  text-align: center;
}

#totalDep h2, #totalRev h2, #balance h2{
  font-weight: 800;
  font-size: 1.5em;
  letter-spacing: -0.05em;
}

#totalDep p, #totalRev p, #balance p{
  font-size: 2em;
  font-weight: 200;
}

#totalRev{
  background-color: #aaf683;
}

#totalDep{
  background-color: #ff6b7e;
}

#balance{
  background-color: #7edbff;
}

#header h3{
  margin: 20px;
  font-weight: 600;
}

#content{
  width: calc(100vw - 300px);
  position: absolute;
  right: 0;
  display: flex;
  flex-flow: row wrap;
  gap: 10px;
  padding: 10px;
}

.cat-container, #add-category, #newCatForm{
  flex: 1 0 auto;
  max-width: 300px;
  background-color: #fff;
  padding: 20px;
}

.cat-container h3{
  color: #e6242d;
  font-size: 1.5em;
}

#revenus-container h3{
  color: #5daf00;
}

.total{
  font-size: 0.9em;
  font-weight: 200;
  letter-spacing: -0.02em;
}

#revenus-list, .depenses-list{
  margin: 15px 0px;
}

.name-depense{
  font-weight: 600;
}

#revenus-form-button, #revenus-delete-button, .dep-form-button, .depenses-delete-button, #addDep-addLine{
  font-size: 1em;
  height: 20px;
  padding: 0px 5px;
  border: none;
  background-color: #eaeaea;
  margin: 3px;
  transition: color 0.5s;
  transition: background-color 0.5s;
}

#revenus-form, .cat-forms{
  width: fit-content;
}

#revenus-form-button, .dep-form-button{
  color: rgb(73, 98, 107);
}

.addDepName, .addDepVal{
  font-size: 0.9em;
  padding: 2px 4px;
  border: 1px solid #aaa;
}

.addDepName::placeholder, .addDepVal::placeholder{
  color: #aaa;
  font-weight: 200;
}

.addDepName:focus, .addDepVal:focus{
  outline-color: #7edbff;
}

.addDepName{
  width: 150px;
  margin-right: 5px;
}

.addDepVal{
  width: 70px;
}

.addDep-buttons{
  margin-top: 10px;
  text-align: center;
}

#revenus-form button, .cat-forms button{
  font-size: 0.9em;
  padding: 2px 7px;
  margin: 8px 5px 3px 0px;
  background-color: #eaeaea;
  border: none;
  transition: all 0.5s;
}

.champ-manquant{
  display: none;
  font-size: 0.8em;
  color: #e6242d;
}

.deleteCat, .delete{
  font-size: 1em;
  border: none;
  padding: 0px 6px;
  margin: 2px;
  font-weight: 800;
  background-color: #eaeaea;
  border-radius: 15px;
  transition: all 0.5s;
}

.deleteCat:hover, .delete:hover{
  background-color: #e6242d;
  cursor: pointer;
}

.deleteCat{
  border-radius: 25px;
}

#add-category{
  text-align: center;
}

#add-category h4{
  font-size: 1.2em;
  margin: 30px 0px;
}

#add-category button{
  font-size: 1.2em;
  border: none;
  background-color: #eaeaea;
  color: rgb(73, 98, 107);
  padding: 4px 8px;
  transition: all 0.5s;
  margin-bottom: 15px;
}

#revenus-form-button:hover, #revenus-delete-button:hover, .dep-form-button:hover, .depenses-delete-button:hover, #revenus-form button:hover, .cat-forms button:hover, #add-category button:hover, #addDep-addLine:hover, #addCat-submit:hover, #addCat-cancel:hover{
  background-color: #7edbff;
  color: #fff;
  cursor: pointer;
}

#newCatForm h4{
  font-size: 1.3em;
  color: #37b2e2;
  margin-bottom: 15px;
}

#addCatName-label, #addDep-label{
  font-weight: 600;
}

#addCatName-label{
  font-size: 1.1em;
}

#addCatName{
  margin: 10px;
  font-size: 0.9em;
  width: 170px;
  border: 1px solid #aaa;
}

#addDep-label{
  margin: 5px 0px;
}

.addDep{
  margin: 3px;
}

#addDep-addLine{
  color: rgb(73, 98, 107);
  margin-bottom: 15px;
}

#addCat-buttons{
  text-align: center;
}

#addCat-submit, #addCat-cancel{
  font-size: 0.9em;
  padding: 3px 7px;
  margin: 0px 5px 10px 5px;
  border: none;
  background-color: #eaeaea;
  transition: all 0.5s;
}

#name-already-used, #aucune-depense{
  color: #e6242d;
  font-size: 0.9em;
  text-align: center;
  margin-bottom: 5px;
}

#newCatForm, .cat-forms, #revenus-form, .delete, .deleteCat, #name-already-used, #aucune-depense{
  display: none;
}

#svg-div{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

svg{
  height: 200px;
  width: 200px;
}

#tooltip{
  background-color: #fff;
  padding: 3px 8px;
  box-shadow: 2px 2px 8px #999;
  font-weight: 600;
}

@media screen and (max-width: 790px){
  #header{
    width: 200px;
  }
  #this-month{
    font-size: 1.1em;
    margin: 15px;
  }
  #totalDep, #totalRev, #balance{
    padding: 8px;
  }
  #totalDep h2, #totalRev h2, #balance h2{
    font-size: 1.3em;
  }
  #totalDep p, #totalRev p, #balance p{
    font-size: 1.7em;
  }
  #header h3{
    font-size: 1em;
  }
  #content{
    width: calc(100vw - 200px);
  }
  .cat-container, #add-category, #newCatForm{
    flex: 1 0 auto;
    max-width: 400px;
  }
  #svg-div{
    margin-top: -20px;
  }
  svg{
    transform: scale(0.8);
  }
}

@media screen and (max-width: 490px){
  #header{
    position: sticky;
    height: min-content;
    width: 100%;
    z-index: 10;
    padding: 8px 0px;
    background-color: #eaeaea;
  }
  #this-month{
    font-size: 0.9em;
    margin: 0 15px 5px 15px;
    padding-top: 20px;
  }
  #header-items{
    display: flex;
    flex-flow: row wrap;
    gap: 5px;
  }
  #totalDep, #totalRev, #balance{
    flex: 1 0 auto;
    margin: 0px;
    padding: 5px;
  }
  #totalDep h2, #totalRev h2, #balance h2{
    font-size: 0.8em;
  }
  #totalDep p, #totalRev p, #balance p{
    font-size: 0.9em;
    font-weight: 400;
  }
  #content{
    width: 100%;
    position: relative;
  }
  .cat-container, #add-category, #newCatForm{
    flex: 1 0 auto;
    max-width: 100%;
    background-color: #fff;
    padding: 12px;
  }
  .total{
    font-size: 0.7em;
  }
  .cat-container h3{
    font-size: 1em;
  }
  .cat-container ul li{
    font-size: 0.8em;
  }
  #revenus-form-button, #revenus-delete-button, .dep-form-button, .depenses-delete-button, #addDep-addLine{
    font-size: 0.9em;
    height: 17px;
    padding: 0px 3px;
  }
  .addDepName, .addDepVal{
    font-size: 0.8em;
    padding: 1px 2px;
  }
  #revenus-form button, .cat-forms button{
    font-size: 0.8em;
    padding: 2px 6px;
    margin: 0px 5px 3px 0px;
  }
  .champ-manquant{
    font-size: 0.7em;
  }
  #add-category h4{
    font-size: 1em;
    margin: 10px 0px;
  }
  
  #add-category button{
    font-size: 1em;
    padding: 3px 6px;
  }
  #newCatForm h4{
    font-size: 1.1em;
    margin-bottom: 5px;
  } 
  #addCatName-label{
    font-size: 0.9em;
  }
  #addCatName{
    margin: 10px;
    font-size: 0.8em;
    padding: 1px 5px;
    width: auto;
  }
  #addDep-label{
    margin: 3px 0px;
    font-size: 0.9em;
  }
  #addDep-addLine{
    margin-bottom: 5px;
  }
  #addCat-submit, #addCat-cancel{
    font-size: 0.8em;
    padding: 2px 5px;
    margin: 0px 3px 10px 3px;
  }
  #name-already-used, #aucune-depense{
    font-size: 0.8em;
  }
  #svg-div, #header h3{
    display:none;
  }
}